@import '../../Variables/variables';

.household {
  width: 340px;
  margin: 0 auto;
  text-align: center;
  @media screen and (min-width: $mb) {
    min-width: 1328px;
  }
  h2 {
    font-size: 20px;
    font-weight: 500;
    @media screen and (min-width: $mb) {
      font-size: 32px;
      text-align: left;
      margin: 24px 0 0;
    }
  }
  .month {
    @media screen and (min-width: $mb) {
      display: flex;
      justify-content: center;
    }
    h3 {
      font-size: 32px;
      font-weight: 600;
      margin-top: 16px;
      margin-bottom: 8px;
      @media screen and (min-width: $mb) {
        font-size: 56px;
        margin-top: 2px;
        margin-bottom: 32px;
        margin-left: 40px;
      }
      .balanceAmountRed {
        color: $amount_red_large;
      }
      .balanceAmountBlue {
        color: $amount_blue_large;
      }
    }
    .amountTotal{
      font-size: 16px;
      margin-top: 0;
      margin-bottom: 16px;
      @media screen and (min-width: $mb) {
        font-size: 32px;
        line-height: 66px;
        margin-left: 40px;
      }
      span {
        color: $amount_red_large;
      }
    }
  }
  .monthMove {
    font-size: 12px;
    font-weight: normal;
    margin-bottom: 16px;
    span {
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
      &:hover {
        opacity: 0.7;
      }
    }
    @media screen and (min-width: $mb) {
      font-size: 16px;
    }
  }
  .householdMap {
    width: 100%;
    max-height: 410px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow-y: scroll;
    margin: 0 auto;
    // スクロールバー非表示
    -ms-overflow-style: none;
    scrollbar-width: none;
    &::-webkit-scrollbar{
      display: none;
    }
    @media screen and (min-width: $mb) {
      width: 784px;
      max-height: 526px;
    }
    .householdBook {
      width: 80px;
      height: 126px;
      display: flex;
      flex-flow: column;
      margin-bottom: 16px;
      @media screen and (min-width: $mb) {
        width: 160px;
        height: 242px;
        margin-bottom: 8px;
      }
      .householdName {
        font-size: 16px;
        font-weight: 500;
        @media screen and (min-width: $mb) {
          font-size: 16px;
          font-weight: 600;
        }
      }
      img {
        width: 80px;
        height: 80px;
        margin-top: 8px;
        margin-bottom: 8px;
        &:hover {
          opacity: 0.7;
        }
        @media screen and (min-width: $mb) {
          width: 144px;
          height: 144px;
          margin-bottom: 16px;
        }
      }
      .amount {
        font-size: 12px;
        font-weight: normal;
        color: $amount_red;
        margin: 0;
        @media screen and (min-width: $mb) {
          font-size: 16px;
          font-weight: 600;
        }
      }
    }
  }
  .button {
    margin: 32px auto 0;
    width: 240px;
    height: 48px;
    background-color: #FFD124;
    border-radius: 8px;
    line-height: 48px;
    cursor: pointer;
    &:hover {
      opacity: 0.7;
    }
    @media screen and (min-width: $mb) {
      width: 480px;
      height: 80px;
      border-radius: 24px;
      font-size: 32px;
      font-weight: bold;
      line-height: 80px;
    }
  }
}