@import '../../Variables/variables';

.sign {
  width: 320px;
  margin: 0 auto;
  @media screen and (min-width: $mb) {
    width: 720px;
  }
  h2 {
    font-size: 20px;
    font-weight: 500;
    margin-top: 24px;
    @media screen and (min-width: $mb) {
      font-size: 32px;
      font-weight: 600;
      margin-top: 44px;
      margin-bottom: 32px;
    }
  }
  .container {
    .signform {
      display: flex;
      flex-flow: column;
      .input {
        margin-bottom: 8px;
        @media screen and (min-width: $mb) {
          margin-bottom: 24px;
        }
        h3 {
          font-size: 16px;
          font-weight: 500;
          margin-top: 0;
          margin-bottom: 8px;
          @media screen and (min-width: $mb) {
            font-size: 24px;
            font-weight: 500;
            margin-bottom: 24px;
          }
        }
        input {
          width: 304px;
          height: 16px;
          border: none;
          outline: none;
          border-radius: 8px;
          padding: 8px;
          color: #535353;
          font-weight: normal;
          &::placeholder {
            font-size: 12px;
            text-align: center;
          }
          @media screen and (min-width: $mb) {
            width: 704px;
            height: 32px;
            border-radius: 16px;
            font-size: 24px;
            font-weight: normal;
            &::placeholder {
              font-size: 24px;
            }
          }
        }
      }
      button {
        width: 160px;
        height: 48px;
        border-radius: 15px;
        border: 0;
        background: #FFD124;
        color: white;
        font-size: 16px;
        position: relative;
        padding: 0;
        margin: 8px auto 0;
        @media screen and (min-width: $mb) {
          width: 480px;
          height: 80px;
          font-size: 32px;
          margin-top: 16px;
        }
        img {
          width: 20px;
          height: 20px;
          position: absolute;
          left: 14px;
          bottom: 14px;
          @media screen and (min-width: $mb) {
            width: 40px;
            height: 40px;
            position: absolute;
            left: 112px;
            bottom: 20px;
          }
        }
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }
  .changebutton {
    text-align: right;
    font-size: 12px;
    margin-top: 8px;
    a {
      color: #FFD124;
      &:hover {
        opacity: 0.7;
      }
    }
    @media screen and (min-width: $mb) {
      margin-top: 24px;
      font-size: 24px;
      font-weight: 500;
    }
  }
}